<template>
  <div id="app">
    <app-header></app-header>
    <el-main>
      <el-row :gutter="10">
        <el-col :xs="0" :sm="1" :md="2" :lg="3">
          <!--  -->
          <div class="grid-content bg-purple-left"></div>
        </el-col>

        <!--  -->
        <el-col :xs="24" :sm="22" :md="20" :lg="18">
          <div class="grid-content bg-purple-middle">
            <el-row>
              <el-col :span="24">
                <span></span>
              </el-col>
            </el-row>

            <el-row :gutter="10">
              <el-col :xs="1" :sm="4" :md="6">
                <span style="visibility:hidden;">empty</span>
              </el-col>

              <el-col :xs="22" :sm="16" :md="12">
                <el-input placeholder="" v-model="qqNumber">
                  <template slot="append">@qq.cm</template>
                </el-input>
              </el-col>

              <el-col :xs="1" :sm="4" :md="6">
                <span style="visibility:hidden;">empty</span>
              </el-col>
            </el-row>

            <el-row :gutter="10">
              <el-col :xs="1" :sm="4" :md="6">
                <span style="visibility:hidden;">empty</span>
              </el-col>

              <el-col :xs="22" :sm="16" :md="12">
                <el-input placeholder="" v-model="password" clearable></el-input>
              </el-col>

              <el-col :xs="1" :sm="4" :md="6">
                <span style="visibility:hidden;">empty</span>
              </el-col>
            </el-row>

            <el-row :gutter="10">
              <el-col :xs="1" :sm="4" :md="6">
                <span style="visibility:hidden;">empty</span>
              </el-col>

              <el-col :xs="22" :sm="16" :md="12">
                <el-input placeholder="" v-model="repassword" clearable></el-input>
              </el-col>

              <el-col :xs="1" :sm="4" :md="6">
                <span style="visibility:hidden;">empty</span>
              </el-col>
            </el-row>

            <el-row>
              <el-col :span="24">
                <span>
                  <el-button style="width:180px;" type="primary" @click="registerToServer"></el-button>
                </span>
              </el-col>
            </el-row>

            <el-row>
              <el-col :span="24">
                <span>
                  <span style="color: gray"></span>
                  <router-link to="/login"></router-link>
                </span>
              </el-col>
            </el-row>
          </div>
        </el-col>

        <el-col :xs="0" :sm="1" :md="2" :lg="3">
          <!--  -->
          <div class="grid-content bg-purple-right"></div>
        </el-col>
      </el-row>
    </el-main>

    <app-footer></app-footer>
  </div>
</template>

<script>
import axios from "axios";

import AppHeader from "@/pages/header/Header";

import AppFooter from "@/pages/footer/Footer";

import { requestURLs } from "@/util/uril_utils";

export default {
  components: {
    AppHeader,
    AppFooter
  },
  name: "Register",
  data() {
    return {
      qqNumber: "",
      password: "",
      repassword: ""
    };
  },
  methods: {
    registerToServer() {
      console.log(
        this.qqNumber + " --- " + this.password + "---" + this.repassword
      );
      var that = this;

      axios({
        method: "post",
        url: requestURLs[3].value,
        data: {
          qqNumber: this.qqNumber,
          password: this.password
        }
      }).then(function(response) {
        console.log(response.data.statusCode);
        if (response.data.status == 1) {
          that.$store.commit('changeQqNumber', that.qqNumber)
          that.$store.commit('changePassword', that.password)
          that.openLoginDialog();
        } else {
          that.openRegisterFailed();
        }
      });

      
    },
    openLoginDialog() {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        center: true
      })
        .then(() => {
          this.$router.push("/login");
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    openRegisterFailed() {
      this.$message.error("");
    }
  },
  mounted() {
    console.log("mounted");
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#app {
  background-color: #f0f2f5;
}
.el-main {
  background-color: #e9eef3;
  color: #333;
  text-align: center;
  z-index: 0;
  height: 600px;
  padding-top: 75px;
  padding-bottom: 40px;
}
.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: #99a9bf;
}

.bg-purple-middle {
  /* background: #ffffff;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1); */

  margin-bottom: 15px;
  padding-top: 10px;
  padding: 25px 35px;
}

.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.grid-content .el-row {
  margin-top: 10px;
}
</style>



