<template>
  <el-row class="user_info_container">
    <el-row class="input-line-row">
      <el-col :span="7" class="input-label"></el-col>
      <el-col :span="14">
        <el-input v-model="taskTitle" placeholder=""></el-input>
      </el-col>
    </el-row>

    <el-row class="input-line-row">
      <el-col :span="7" class="input-label"></el-col>
      <el-col :span="10">
        <el-select style="float:left;" v-model="taskType" placeholder="">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-col>
    </el-row>

    <el-row class="input-line-row">
      <el-col :span="7" class="input-label"></el-col>
      <el-col :span="10">
        <el-date-picker style="float:left;" v-model="taskEndTime" type="date" placeholder=""></el-date-picker>
      </el-col>
    </el-row>

    <el-row class="input-line-row">
      <el-col :span="7" class="input-label"></el-col>
      <el-col :span="14">
        <el-input type="textarea" :rows="8" placeholder="" v-model="taskDescribe"></el-input>
      </el-col>
    </el-row>

    <el-row class="input-line-row">
      <el-col :span="7" class="input-label"></el-col>
      <el-col :span="14">
        <el-input type="textarea" :rows="4" placeholder="" v-model="taskRequire"></el-input>
      </el-col>
    </el-row>

    <el-row class="input-line-row">
      <el-col :span="7" class="input-label"></el-col>
      <el-col :span="14">
        <el-input type="textarea" :rows="3" placeholder="" v-model="reward"></el-input>
      </el-col>
    </el-row>

    <el-row class="input-line-row">
      <el-col :span="7" class="input-label">
        <p style="visibility:hidden;">-</p>
      </el-col>
      <el-col :span="10">
        <el-button
          type="primary"
          size="medium"
          style="float: left; margin-top: 5px; width: 150px;"
          @click="submitTaskToServer"
        ></el-button>
      </el-col>
    </el-row>
  </el-row>
</template>

<script>
import { requestURLs } from "@/util/uril_utils";

export default {
  name: "PublishTask",
  data() {
    return {
      options: [
        {
          name: "1",
          value: "1",
          label: ""
        },
        {
          name: "1",
          value: "2",
          label: ""
        },
        {
          name: "1",
          value: "3",
          label: ""
        },
        {
          name: "1",
          value: "4",
          label: ""
        },
        {
          name: "1",
          value: "5",
          label: ""
        }
      ],
      value: "",

      taskId: "",
      userId: "",
      taskTitle: "",
      taskType: "",
      taskRequire: "",
      taskDescribe: "",
      reward: "",
      taskState: "",
      taskEndTime: "",
      taskSubmitTime: ""
    };
  },
  methods: {
    submitTaskToServer() {
      // var token = document.cookie.split("=")[1];

      console.log("click to submit");
      let that = this;
      console.log(that.guuid());
      let newTask = JSON.stringify({
        taskId: that.guuid(),
        userId: that.$store.state.userId,
        userName: that.$store.state.userName,
        taskTitle: this.taskTitle,
        taskType: this.taskType,
        taskRequire: this.taskRequire,
        taskDescribe: this.taskDescribe,
        reward: this.reward,
        taskState: 2,
        taskEndTime: this.taskEndTime,
        taskSubmitTime: new Date().getTime()
      });
      console.log(newTask);

      $.ajax({
        type: "post",
        url: requestURLs[0].value,
        contentType: "application/json",
        dataType: "json",
        headers: { Authorization: "Bearer " + that.$store.state.token },
        withCredentials: true,
        data: newTask,
        success: function(result) {
          console.log(result);
          if (result.status == 1) {
            that.$message({
              showClose: true,
              message: "!",
              type: "success"
            });
          } else {
            that.$message.error("");
          }
        },
        error: function(XMLHttpResponse, textStatus, errorThrown) {
          that.$message.error(textStatus + " : " + XMLHttpResponse.message);
        }
      });
    },
    guuid() {
      return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, function(
        c
      ) {
        var r = (Math.random() * 16) | 0,
          v = c == "x" ? r : (r & 0x3) | 0x8;
        return v.toString(16);
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.user_info_container {
  padding: 20px 20px;
  color: #727f8e;
}

.input-line-row {
  margin-bottom: 10px;
  line-height: 60px;
}
.input-label {
  text-align: right;
}
</style>



